"use client";
import { useState } from "react";
import { useStore } from "@/store";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from "@/components/ui/dialog";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import type { Vaccine } from "@/types/vaccine";
import type { Category } from "@/types/category";
import { useCategories } from "@/hooks/useCategories";
import { Label } from "../ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Checkbox } from "@/components/ui/checkbox";

interface VaccineModalProps {
  onClose: () => void;
  vaccine?: Vaccine;
}

export function VaccineModal({ onClose, vaccine }: VaccineModalProps) {
  const { createVaccine, updateVaccine } = useStore();
  const { categories } = useCategories();

  const isUpdateMode = Boolean(vaccine);

  // Form state
  const [formData, setFormData] = useState({
    title: vaccine?.title || "",
    categoryId: vaccine?.categoryId?.toString() || "",
    price: vaccine?.price || 0,
    stock: vaccine?.stock || 0,
    description: vaccine?.description || "",
    discount: vaccine?.discount || 0,
    discountPrice: vaccine?.discountPrice || 0,
    manufacturer: vaccine?.manufacturer || "",
    targetGroup: vaccine?.targetGroup || "",
    schedule: vaccine?.schedule || "",
    sideEffects: vaccine?.sideEffects || "",
    isActive: vaccine?.isActive ?? true,
    available: vaccine?.available ?? true,
    quantity: vaccine?.stock || 0,
    numberOfDoses: vaccine?.numberOfDoses || 1,
    minAgeMonths: vaccine?.minAgeMonths || 0,
    maxAgeMonths: vaccine?.maxAgeMonths || 0,
    minDaysBetweenDoses: vaccine?.minDaysBetweenDoses || 0,
    isPriority: vaccine?.isPriority || false
  });

  // Handle input changes
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value, type } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'number' ? Number(value) : value
    });
  };

  // Handle select changes
  const handleSelectChange = (name: string, value: string) => {
    setFormData({
      ...formData,
      [name]: value
    });
  };

  // Handle checkbox changes
  const handleCheckboxChange = (name: string, checked: boolean) => {
    setFormData({
      ...formData,
      [name]: checked
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const formDataToSubmit = new FormData();
    formDataToSubmit.append("title", formData.title);
    if (formData.categoryId) {
      formDataToSubmit.append("categoryId", formData.categoryId);
    }
    formDataToSubmit.append("price", formData.price.toString());
    formDataToSubmit.append("stock", formData.stock.toString());
    formDataToSubmit.append("description", formData.description);
    formDataToSubmit.append("discount", formData.discount.toString());
    formDataToSubmit.append("discountPrice", formData.discountPrice.toString());
    formDataToSubmit.append("manufacturer", formData.manufacturer);
    formDataToSubmit.append("targetGroup", formData.targetGroup);
    formDataToSubmit.append("schedule", formData.schedule);
    formDataToSubmit.append("sideEffects", formData.sideEffects);
    formDataToSubmit.append("isActive", JSON.stringify(formData.isActive));
    formDataToSubmit.append("available", JSON.stringify(formData.available));
    formDataToSubmit.append("quantity", formData.quantity.toString());
    formDataToSubmit.append("numberOfDoses", formData.numberOfDoses.toString());
    formDataToSubmit.append("minAgeMonths", formData.minAgeMonths.toString());
    formDataToSubmit.append("maxAgeMonths", formData.maxAgeMonths.toString());
    formDataToSubmit.append("minDaysBetweenDoses", formData.minDaysBetweenDoses.toString());
    formDataToSubmit.append("isPriority", JSON.stringify(formData.isPriority));

    try {
      if (isUpdateMode && vaccine) {
        await updateVaccine(vaccine.id, formDataToSubmit);
      } else {
        await createVaccine(formDataToSubmit);
      }
      onClose();
    } catch (error) {
      console.error("Error submitting vaccine form:", error);
    }
  };

  return (
    <Dialog open onOpenChange={onClose}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>{isUpdateMode ? "Update Vaccine" : "Create Vaccine"}</DialogTitle>
          <DialogDescription>
            {isUpdateMode
              ? "Modify the vaccine details below."
              : "Fill in the details below to create a new vaccine."}
          </DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="title">Vaccine Name</Label>
            <Input id="title" name="title" value={formData.title} onChange={handleChange} required />
          </div>

          <div className="space-y-2">
            <Label htmlFor="categoryId">Category</Label>
            <Select name="categoryId" value={formData.categoryId} onValueChange={(value) => handleSelectChange("categoryId", value)}>
              <SelectTrigger>
                <SelectValue placeholder="Select category" />
              </SelectTrigger>
              <SelectContent>
                {categories?.map((category: Category) => (
                  <SelectItem key={category.id} value={category.id.toString()}>
                    {category.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label htmlFor="manufacturer">Manufacturer</Label>
            <Input id="manufacturer" name="manufacturer" value={formData.manufacturer} onChange={handleChange} required />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="price">Price</Label>
              <Input
                id="price"
                name="price"
                type="number"
                min="0"
                step="0.01"
                value={formData.price}
                onChange={handleChange}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="discount">Discount (%)</Label>
              <Input
                id="discount"
                name="discount"
                type="number"
                min="0"
                max="100"
                value={formData.discount}
                onChange={handleChange}
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="quantity">Quantity</Label>
              <Input
                id="quantity"
                name="quantity"
                type="number"
                min="0"
                value={formData.quantity}
                onChange={handleChange}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="numberOfDoses">Number of Doses</Label>
              <Input
                id="numberOfDoses"
                name="numberOfDoses"
                type="number"
                min="1"
                value={formData.numberOfDoses}
                onChange={handleChange}
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="minAgeMonths">Min Age (months)</Label>
              <Input
                id="minAgeMonths"
                name="minAgeMonths"
                type="number"
                min="0"
                value={formData.minAgeMonths}
                onChange={handleChange}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="maxAgeMonths">Max Age (months)</Label>
              <Input
                id="maxAgeMonths"
                name="maxAgeMonths"
                type="number"
                min="0"
                value={formData.maxAgeMonths}
                onChange={handleChange}
              />
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="minDaysBetweenDoses">Min Days Between Doses</Label>
            <Input
              id="minDaysBetweenDoses"
              name="minDaysBetweenDoses"
              type="number"
              min="0"
              value={formData.minDaysBetweenDoses}
              onChange={handleChange}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="targetGroup">Target Group</Label>
            <Input id="targetGroup" name="targetGroup" value={formData.targetGroup} onChange={handleChange} />
          </div>

          <div className="space-y-2">
            <Label htmlFor="description">Description</Label>
            <Textarea id="description" name="description" value={formData.description} onChange={handleChange} rows={3} />
          </div>

          <div className="space-y-2">
            <Label htmlFor="schedule">Schedule</Label>
            <Textarea
              id="schedule"
              name="schedule"
              value={formData.schedule}
              onChange={handleChange}
              rows={2}
              placeholder="E.g., First dose at 2 months, second dose at 4 months..."
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="sideEffects">Side Effects</Label>
            <Textarea id="sideEffects" name="sideEffects" value={formData.sideEffects} onChange={handleChange} rows={2} />
          </div>

          <div className="flex items-center space-x-2">
            <Checkbox
              id="isActive"
              checked={formData.isActive}
              onCheckedChange={(checked) => handleCheckboxChange("isActive", checked as boolean)}
            />
            <Label htmlFor="isActive" className="cursor-pointer">
              Active
            </Label>
          </div>

          <div className="flex items-center space-x-2">
            <Checkbox
              id="isPriority"
              checked={formData.isPriority}
              onCheckedChange={(checked) => handleCheckboxChange("isPriority", checked as boolean)}
            />
            <Label htmlFor="isPriority" className="cursor-pointer">
              Priority
            </Label>
          </div>

          <div className="flex justify-end space-x-2 pt-4">
            <Button type="button" variant="outline" onClick={onClose}>
              Cancel
            </Button>
            <Button type="submit">{isUpdateMode ? "Update" : "Create"} Vaccine</Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
